<template>
  <div id="my-header">
    <Header class="ivu-menu-light">
      <Menu mode="horizontal" theme="light" :active-name="activeIndex" width="auto" @on-select="handleSelect">
        <div class="layout-logo"><h1><Icon type="logo-octocat" />无定喵</h1></div>
        <div class="layout-nav">
          <MenuItem name="home">
            <Icon type="ios-home" />首页
          </MenuItem>
          <MenuItem name="blog">
            <Icon type="md-paw" />技术博客
          </MenuItem>
          <MenuItem name="work">
            <Icon type="md-game-controller-b" />作品合集
          </MenuItem>
          <MenuItem name="about">
            <Icon type="logo-html5" />关于本站
          </MenuItem>
        </div>
      </Menu>
    </Header>
    <div class="line"></div>
  </div>
</template>

<script>

export default {
  name: 'MyHeader',
  data () {
    return {
      activeIndex: 'home'
    }
  },
  methods: {
    handleSelect (key) {
      this.$router.push(key)
    }
  },
  created () {
    var activeIndex = this.$router.history.current.path.split('/')[1]
    if (activeIndex === '')activeIndex = 'home'
    this.activeIndex = activeIndex
  }
}
</script>

<style scoped>
  .el-header {
    padding:0 !important;
  }
  .line{
    border-bottom: solid 1px #e6e6e6;
  }
  .linkto{
    text-decoration: none;
  }
  .layout-logo{
    width: 120px;
    height: 30px;
    background: #fff;
    border-radius: 3px;
    float: left;
    position: relative;
    top: 15px;
    left: 80px;
  }
  .layout-logo h1{
    height: 30px;
    line-height: 30px;
  }
  .layout-nav{
    width: 450px;
    margin: 0 auto;
    margin-right: 20px;
    margin-left: 280px;
  }
  .ivu-layout-header {
    height: 60px;
    padding:0;
  }
</style>



